#login-root {
  flex-direction: column;
  display: flex;
}
#login-root #top-view {
  width: 100%;
  flex: 4.5;
  background-color: #009688;
  flex-direction: column;
  display: flex;
  align-items: center;
  justify-content: center;
}
#login-root #top-view #app-title {
  margin-top: 30px;
  color: white;
  font-size: 1.6rem;
  text-decoration: none;
  border-bottom: 2px solid red;
}
#login-root #top-view #app-info {
  margin-top: 40px;
  color: white;
  font-size: 0.8rem;
}
#login-root #bottom-view {
  width: 100%;
  flex: 5.5;
  background-color: #f6f6f6;
  flex-direction: column;
  display: flex;
  align-items: center;
}
#login-root #bottom-view #input-username {
  margin-top: 30px;
  width: 85%;
  height: 30px;
  background-color: #f6f6f6;
  border: 0px;
  outline: none;
  color: #009688;
  border-bottom: 2px solid red;
}
#login-root #bottom-view #input-password {
  margin-top: 20px;
  width: 85%;
  height: 30px;
  background-color: #f6f6f6;
  border: 0px;
  outline: none;
  color: #009688;
  border-bottom: 2px solid red;
}
#login-root #bottom-view #login-container {
  width: 85%;
  margin-top: 10px;
  display: flex;
  height: 100px;
  flex-direction: column;
  position: relative;
}
#login-root #bottom-view #login-container #btn-login {
  margin-top: 20px;
  height: 45px;
  width: 100%;
  color: white;
  text-align: center;
  line-height: 45px;
  font-size: 1rem;
  background-color: #009688;
}
#login-root #bottom-view #login-container #forget-password {
  position: absolute;
  bottom: 10px;
  margin-top: 10px;
  right: 0px;
  color: #009688;
  text-align: center;
  font-size: 0.7rem;
}
#login-root #bottom-view #team-info {
  position: absolute;
  bottom: 5px;
  align-self: center;
  font-size: 0.6rem;
  color: #7f7f7f;
}
input::-webkit-input-placeholder {
  color: #009688;
  font-size: 13px;
}
