#lost-and-found-root-view {
  display: flex;
  flex-direction: column;
  width: 100%;
  position: relative;
}
#lost-and-found-root-view #user-info {
  display: flex;
  flex-direction: row;
}
#lost-and-found-root-view #user-info #user-header-img {
  width: 50px;
  border-radius: 25px;
  margin: 20px;
  height: 50px;
}
#lost-and-found-root-view #user-info #user-name {
  display: flex;
  flex-direction: column;
}
#lost-and-found-root-view #user-info #user-name :first-child {
  font-size: 1rem;
  color: #000;
}
#lost-and-found-root-view #user-info #user-name :last-child {
  margin-top: 10px;
  font-size: 0.5rem;
  color: #999999;
}
#lost-and-found-root-view #type-lost {
  margin-left: 15px;
}
#lost-and-found-root-view #type-lost :first-child {
  color: #666666;
  font-weight: bolder;
  font-size: 1.4rem;
}
#lost-and-found-root-view #type-lost :last-child {
  color: #000;
  font-family: Arial;
  font-size: 1rem;
}
#lost-and-found-root-view #lost-content {
  margin-left: 15px;
  font-size: 0.8rem;
  color: #666666;
}
#lost-and-found-root-view .goods-url {
  width: 100%;
  margin-top: 10px;
}
#lost-and-found-root-view .img-tips {
  margin-left: 15px;
  font-size: 0.8rem;
  color: #4d4d4d;
}
#lost-and-found-root-view #bottom-btn {
  position: fixed;
  text-align: center;
  line-height: 50px;
  color: white;
  height: 50px;
  bottom: 0px;
}
#lost-and-found-root-view #bottom-btn :first-child {
  width: 60%;
  background-color: #4d4d4d;
}
#lost-and-found-root-view #bottom-btn :last-child {
  width: 40%;
  background-color: #009688;
}
