#goods-root-view {
  display: flex;
  flex-direction: column;
}
#user-info {
  display: flex;
  flex-direction: row;
  align-items: center;
  width: 100%;
  height: 120px;
}
#goods-info {
  flex-direction: column;
  display: flex;
}
#user-head-img {
  width: 50px;
  border-radius: 25px;
  margin: 20px;
  height: 50px;
}
#user-name {
  font-size: 1rem;
  color: black;
}
#goods-time {
  font-size: 0.5rem;
  color: #7f7f7f;
}
#goods-img {
  width: 100%;
  margin-top: 5px;
}
#goods-desc {
  display: flex;
  flex-direction: row;
}
#goods-title {
  margin: 15px;
  font-size: 1.1rem;
  color: black;
}
#goods-content {
  margin: 15px;
  font-size: 0.8rem;
  color: #666666;
}
#goods-price {
  color: red;
  margin: 10px;
  font-size: 1.3rem;
}
#goods-sold-out {
  width: 65px;
  height: 60px;
  position: absolute;
  right: 10px;
}
#bottom {
  height: 65px;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-left: 5px;
  margin-right: 5px;
  background-color: #99999999;
  position: fixed;
  bottom: 0;
}
#app_logo {
  height: 40px;
  width: 40px;
  align-self: center;
  margin-left: 10px;
}
#download-now {
  background-color: #009688;
  color: #fff;
  align-self: center;
  text-align: center;
  font-size: 0.6rem;
  width: fit-content;
  padding-left: 10px;
  padding-top: 5px;
  padding-bottom: 5px;
  padding-right: 10px;
  margin-right: 20px;
}
#herf-app {
  text-decoration: none;
  color: #fff;
}
#bottom-app-info {
  display: flex;
  flex-direction: row;
}
#app-info {
  justify-content: center;
  margin-left: 15px;
  display: flex;
  flex-direction: column;
}
#app-name {
  color: #f6f6f6;
  font-size: 1rem;
}
#app-desc {
  margin-top: 2px;
  color: #f6f6f6;
  font-size: 0.5rem;
}
#bottom-btn {
  height: 50px;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  position: fixed;
  bottom: 0;
}
#scan-user-info {
  width: 60%;
  height: 50px;
  background-color: #4d4d4d;
  text-align: center;
  line-height: 50px;
  color: white;
}
#chat-to-user {
  width: 40%;
  height: 50px;
  text-align: center;
  line-height: 50px;
  color: white;
  background-color: #009688;
}
