body,
html {
  width: 100%;
  height: 100%;
  background-color: white;
}
#dynamic-root {
  position: relative;
  height: 100%;
}
#dynamic-root #dynamic-card {
  margin-top: 30px;
  background-color: white;
  height: 40%;
}
#dynamic-root #dynamic-card #new-dynamic-info {
  margin-left: 20px;
  margin-top: 20px;
  color: #000000;
  font-weight: bold;
  font-size: 0.9rem;
}
#dynamic-root #dynamic-card #dynamic-author {
  margin-left: 10px;
  color: black;
}
#dynamic-root #dynamic-card #dynamic-time {
  position: absolute;
  font-size: 0.6rem;
  color: #818181;
  right: 10px;
}
#dynamic-root #dynamic-card #dynamic-content {
  margin-top: 15px;
  margin-left: 15px;
  font-size: 0.7rem;
  letter-spacing: 1px;
  color: #999999;
}
#dynamic-root #dynamic-card #reply-count {
  display: flex;
}
#dynamic-root #dynamic-card #reply-count .dynamic-reply-img {
  width: 20px;
  height: 20px;
  margin-top: 10px;
  margin-left: 15px;
}
#dynamic-root #dynamic-card #reply-count .dynamic-reply-text {
  font-size: 0.8rem;
  color: #818181;
  line-height: 20px;
  margin-left: 5px;
  margin-top: 8px;
}
#dynamic-root #dynamic-card #img-contaniner {
  display: flex;
  flex-direction: row;
}
#dynamic-root #dynamic-card #img-contaniner img {
  margin: 10px;
  width: 100px;
  height: 100px;
}
#dynamic-root #comment-item {
  padding: 10px;
}
#dynamic-root #comment-item #comment-index {
  display: flex;
  flex-direction: column;
}
#dynamic-root #comment-item #comment-index #commenter-time {
  margin-top: 6px;
  font-size: 0.3rem;
  color: #818181;
  margin-bottom: 5px;
}
#dynamic-root #comment-item #comment-content {
  font-size: 1rem;
  color: #818181;
}
#dynamic-root #round-btn {
  width: 50px;
  height: 50px;
  text-align: center;
  vertical-align: center;
  position: fixed;
  border-radius: 25px;
  background-color: #b1b107;
  bottom: 30px;
  right: 15px;
}
#dynamic-root #round-btn #new-pen {
  align-content: center;
  align-items: center;
  align-self: center;
  margin-top: 12px;
  margin-left: 2px;
  width: 25px;
  height: 25px;
}
#dynamic-root #root {
  margin-top: 50px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
#dynamic-root #root #comment-input {
  position: relative;
  border-radius: 5px;
  width: 85%;
  height: 80px;
  background-color: #e3cbf8;
}
#dynamic-root #root #release-comment {
  margin-top: 15px;
  width: 95%;
  color: red;
  font-size: 0.8rem;
  height: 35px;
  text-align: center;
  line-height: 35px;
  background-color: #f6f6f6;
}
