html,
body {
  background-color: white;
}
#intro-root-view {
  display: flex;
  flex-direction: column;
}
#intro-root-view .img-content {
  width: 100%;
}
#intro-root-view #btn-container {
  background-color: #36353b;
  width: 100%;
  background-size: 100% auto;
  display: flex;
  height: 600px;
  flex-direction: column;
}
#intro-root-view #btn-container #app-info {
  font-weight: bold;
  margin-top: 80px;
  color: white;
  font-family: Arial;
  font-size: 1rem;
}
#intro-root-view #btn-container img {
  height: 180px;
  width: 180px;
  margin: 0px auto;
  margin-top: 80px;
}
#intro-root-view #btn-container #we-chat-info {
  color: white;
  font-family: Arial, "Helvetica Neue", Helvetica, sans-serif;
  margin-top: 35px;
  font-size: 0.8rem;
}
#intro-root-view #btn-container #down-btn {
  font-weight: bold;
  width: 160px;
  margin-top: 35px;
  text-align: center;
  padding: 15px 20px;
  color: #666666;
  border-radius: 20px;
  background: white;
}
#intro-root-view .img-title {
  width: 30%;
  margin: 20px auto;
}
#intro-root-view span {
  margin: 0px auto;
  color: #999999;
}
#intro-root-view #about-us {
  font-size: 1rem;
  margin-top: 30px;
  padding: 5px;
}
#intro-root-view #copyright {
  margin-top: 5px;
  margin-bottom: 10px;
  font-size: 0.8rem;
}
