#picture-root {
  display: flex;
  flex-direction: column;
}
#user-info {
  display: flex;
  flex-direction: row;
  align-items: center;
  width: 100%;
  height: 120px;
}
#user-name {
  font-size: 1rem;
  color: black;
}
#picture-time {
  font-size: 0.5rem;
  color: #7f7f7f;
}
#user-header-img {
  width: 40px;
  border-radius: 20px;
  margin: 20px;
  height: 40px;
}
#user-name-and-time {
  display: flex;
  flex-direction: column;
}
#picture {
  width: 98%;
  height: 80%;
  align-self: center;
}
#bottom {
  height: 65px;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  /*margin-left: 5px;*/
  /*margin-right: 5px;*/
  background-color: #99999999;
  position: fixed;
  bottom: 0;
}
#app_logo {
  height: 40px;
  width: 40px;
  align-self: center;
  margin-left: 10px;
}
#download-now {
  background-color: #009688;
  color: #fff;
  align-self: center;
  text-align: center;
  font-size: 0.6rem;
  width: fit-content;
  padding-left: 10px;
  padding-top: 5px;
  padding-bottom: 5px;
  padding-right: 10px;
  margin-right: 20px;
}
#herf-app {
  text-decoration: none;
  color: #fff;
}
#bottom-app-info {
  display: flex;
  flex-direction: row;
}
#app-info {
  justify-content: center;
  margin-left: 15px;
  display: flex;
  flex-direction: column;
}
#app-name {
  color: #f6f6f6;
  font-size: 1rem;
}
#app-desc {
  margin-top: 2px;
  color: #f6f6f6;
  font-size: 0.5rem;
}
